import {
	mapState
} from "vuex";
export default {
	data() {
		return {
			staticURL: this.$url.staticURL,
			uploadURL: this.$url.uploadURL,
			list: [], //商品列表
		};
	},
	computed: {
		...mapState(['scanAll'])
	},
	created() {
		this.list = this.scanAll.cart;
		console.log(this.list);
	},
	methods: {
		change(type, index) {
			const vm = this;
			let num = vm.list[index].num;
			if (type == 'reduce') { // 减
				num == 1 ? (num = 1) : (num -= 1);
			} else if (type == 'add') { // 加
				num += 1;
			}
			vm.list[index].num = num;
		}
	}
}
